import React from 'react'
import "../styles/Quote.css";
import { useState, useEffect } from "react";

const Quote = () => {
    const [hoverQuote, setHoverQuote] = useState(false);


    return (
        <div>
        <div className="quoteContainer test"           onMouseOver={() => setHoverQuote(true)}
        onMouseOut={() => setHoverQuote(false)}
        >
            <p className={`quote ${hoverQuote ? "slideUp" : ""}`}>Quote Here Random Quote etc etc etc</p>
            {/* <p className="author">Author here</p> */}
        </div>
        <div className="quoteContainer">
            <p className={`author ${hoverQuote ? "slideDownFadeIn" : ""}`}>Author here</p>
        </div>
        </div>
    )
}

export default Quote
